footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2em;
    background-color: black;
    color: white;
}

footer img {
    width: 100px;
    margin-bottom: 1em;
}

footer p {
    font-size: 24px;
}

@media (max-width: 768px) {

    
   footer {
    display: flex;
    flex-direction: column;
    align-items: center;
   }

   footer p {
    text-align: center;
   }
}
    
